:root {
    --softRed: hsl(7, 99%, 70%);
    --yellow: hsl(51, 100%, 49%);
    --darkDesaturatedCyan: hsl(167, 40%, 24%); // graphic design text
    --darkBlue: hsl(198, 62%, 26%); //photography text
    --darkModerateCyan: hsl(168, 34%, 41%); //footer
