//769px, 1040px, 1400px
$breakpoints-up: ("medium": "48.0625em", "large": "65em", "xlarge": "87.5em");

//768px, 1024px, 1399px
$breakpoints-down: ("small": "48em", "medium": "64em", "large": "87.4375em");

@mixin breakpoint-up($size) {
    @media (min-width: map-get($breakpoints-up, $size)) {
        @content;
    }
}

@mixin breakpoint-down($size) {
    @media (max-width: map-get($breakpoints-down, $size)) {
        @content;
    }
}


