@use '../util' as *;

.footer {
    background-color: hsl(168, 44%, 70%);
    
    &__items {
        width: rem(297);
        margin: 0 auto;
        padding: rem(72) 0;
    }

    &__logo {
        margin: 0 auto;
    }

    &__info-links {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: rem(40);
    }

    &__info-link { 
        color: var(--darkModerateCyan); 

        &:hover {
            color: var(--white);
        }
    }

    &__socials {
        display: flex;
        gap: rem(28);
        justify-content: center;
        align-items: center;
        padding-top: rem(88);

        .social-icon {
            transition: filter 300ms ease-in-out;
        }

        .social-icon:hover {
            filter: brightness(0) invert(1);
        }
    }
}