@use '../util' as *;

*, *::before, *::after {
    box-sizing: border-box;
}

body {
    width: 100%;
    padding: 0;
    margin: 0;
    font-family: var(--font-barlow);
    font-size: rem(18);
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
}

img {
    display: block;
    max-width: 100%;
}

.noscroll {
    overflow: hidden;
}