@use '../util' as *;

.header {
    width: 100%;
    height: rem(800);
    background-image: url('../images/desktop/image-header.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;

    @include breakpoint-down(small) {
        height: rem(538);
        background-image: url('../images/mobile/image-header.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: top center;
    }

    &__logo {
        width: 100%;

        @include breakpoint-down(small) {
            display: none;
        }
    }

    &__logo-mobile {
        width: 100%;
        display: none;

        @include breakpoint-down(small) {
            display: block;
        }
    }

    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: rem(34) rem(48) 0 rem(40);

        @include breakpoint-down(small) {
            padding: rem(32) rem(24) 0 rem(24);
        }
    }

    &__nav-container {
        display: flex;
        gap: rem(47);
        align-items: center;

        @include breakpoint-down(medium) {
            display: none;
        }
    }
    
    &__nav {
        display: flex;
        gap: rem(47);
        align-items: center;
    }
    
    &__nav--link {
        color: var(--white);
    }

    &__mobile-menu {
        display: none;
        position: relative;

        @include breakpoint-down(medium) {
            display: block;
        }

        span {
            display: block;
            width: rem(24);
            height: rem(2);
            background-color: var(--white);
            margin-bottom: rem(6);
        }

        span:last-child {
            margin-bottom: 0;
        }
    }

    &__mobile-nav--container {
        position: absolute;
        top: rem(52);
        right: 0;
        width: rem(329);
        background-color: hsl(34, 100%, 99%);
        text-align: center;

        &::before {
            position: absolute;
            top: rem(-24);
            right: 0;
            content: '';
            width: 0;
            height: 0;
            border-bottom: rem(24) solid hsl(34, 100%, 99%);
            border-left: rem(24) solid transparent;
        }
    }

    &__nav-mobile {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: rem(32);
        margin-top: rem(39);
    }

    &__nav-mobile--link {
        color: var(--darkGrayishBlue);
    }
        

    .mobile-btn {
        background-color: var(--yellow);
        border: 1px solid var(--yellow);
        margin-top: rem(32);
        margin-bottom: rem(39);
    }
    

    &__contact-button {
        border: 1px solid var(--white);
        border-radius: rem(28);
        background-color: var(--white);
        padding: rem(16) rem(30) rem(15) rem(32);
        font-family: var(--font-fraunces);
        font-weight: 700;
        font-size: rem(15);
        line-height: rem(25);
        letter-spacing: -0.11px;
        color: var(--veryDarkDesaturatedBlue);
        text-transform: uppercase;
        cursor: pointer;
        transition: all 250ms ease-in-out;

        &:hover {
            border: 1px solid hsl(0, 0%, 100%, 0.1);
            background-color: hsl(0, 0%, 100%, 0.2);
            color: var(--white);
        }
    }

    &__title {
        text-align: center;
        padding-top: rem(105);

        @include breakpoint-down(medium) {
            padding-top: rem(88);
        }
    }

    &__arrow {
        width: rem(30);
        padding-top: rem(96.54);
        margin: 0 auto;

        @include breakpoint-down(medium) {
            padding-top: rem(52);
        }
    }

    .hidden {
        display: none;
    }

    .hamburger-menu {
        cursor: pointer;
    }
}