@use '../util' as *;

h1, h2, h3, h4 {
    font-family: var(--font-fraunces);
    font-weight: 900;
    margin: 0;
}

h1 {
    color: var(--white);
    font-size: rem(56);
    line-height: rem(69);
    letter-spacing: 8.75px;
    text-transform: uppercase;

    @include breakpoint-down(small) {
        font-size: rem(40);
        line-height: rem(49.32);
        letter-spacing: 6.25px;
    }
}

h2 {
    color: var(--veryDarkDesaturatedBlue);
    font-size: rem(40);
    line-height: rem(49.32);
    letter-spacing: -0.29px;
    margin-bottom: 1.5em;

    @include breakpoint-down(small) {
        font-size: rem(32);
        line-height: rem(39.46);
        margin-bottom: 1.2em;
    }
}

h3 {
    font-size: rem(28);
    line-height: rem(34.52);
    letter-spacing: -0.2px;
    margin-bottom: rem(27);
}

h4 {
    font-size: rem(20);
    line-height: rem(24.66);
    letter-spacing: 5px;
    text-transform: uppercase;
    color: var(--grayishBlue);
    text-align: center;
    padding-top: rem(160);

    @include breakpoint-down(medium) {
        padding-top: rem(64);
    }

    @include breakpoint-down(small) {
        font-size: rem(16);
        line-height: rem(19.73);
        letter-spacing: 4px;
    }
}

p {
    font-weight: 600;
    line-height: rem(30);
    letter-spacing: -0.13px;
    margin: 0;
}

.info-link {
    font-weight: 600;
    line-height: rem(25);
    letter-spacing: -0.13px;
    transition: color 300ms ease-in-out;
}