:root {
  --softRed: hsl(7, 99%, 70%);
  --yellow: hsl(51, 100%, 49%);
  --darkDesaturatedCyan: hsl(167, 40%, 24%);
  --darkBlue: hsl(198, 62%, 26%);
  --darkModerateCyan: hsl(168, 34%, 41%);
  --veryDarkDesaturatedBlue: hsl(212, 27%, 19%);
  --veryDarkGrayishBlue: hsl(213, 9%, 39%);
  --darkGrayishBlue: hsl(232, 10%, 55%);
  --grayishBlue: hsl(210, 4%, 67%);
  --white: hsl(0, 0%, 100%);
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  width: 100%;
  padding: 0;
  margin: 0;
  font-family: var(--font-barlow);
  font-size: 1.125rem;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

.noscroll {
  overflow: hidden;
}

:root {
  --font-barlow: "Barlow", sans-serif;
  --font-fraunces: "Fraunces", serif;
}

h1, h2, h3, h4 {
  font-family: var(--font-fraunces);
  font-weight: 900;
  margin: 0;
}

h1 {
  color: var(--white);
  font-size: 3.5rem;
  line-height: 4.3125rem;
  letter-spacing: 8.75px;
  text-transform: uppercase;
}
@media (max-width: 48em) {
  h1 {
    font-size: 2.5rem;
    line-height: 3.0825rem;
    letter-spacing: 6.25px;
  }
}

h2 {
  color: var(--veryDarkDesaturatedBlue);
  font-size: 2.5rem;
  line-height: 3.0825rem;
  letter-spacing: -0.29px;
  margin-bottom: 1.5em;
}
@media (max-width: 48em) {
  h2 {
    font-size: 2rem;
    line-height: 2.46625rem;
    margin-bottom: 1.2em;
  }
}

h3 {
  font-size: 1.75rem;
  line-height: 2.1575rem;
  letter-spacing: -0.2px;
  margin-bottom: 1.6875rem;
}

h4 {
  font-size: 1.25rem;
  line-height: 1.54125rem;
  letter-spacing: 5px;
  text-transform: uppercase;
  color: var(--grayishBlue);
  text-align: center;
  padding-top: 10rem;
}
@media (max-width: 64em) {
  h4 {
    padding-top: 4rem;
  }
}
@media (max-width: 48em) {
  h4 {
    font-size: 1rem;
    line-height: 1.233125rem;
    letter-spacing: 4px;
  }
}

p {
  font-weight: 600;
  line-height: 1.875rem;
  letter-spacing: -0.13px;
  margin: 0;
}

.info-link {
  font-weight: 600;
  line-height: 1.5625rem;
  letter-spacing: -0.13px;
  transition: color 300ms ease-in-out;
}

.header {
  width: 100%;
  height: 50rem;
  background-image: url("../images/desktop/image-header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
}
@media (max-width: 48em) {
  .header {
    height: 33.625rem;
    background-image: url("../images/mobile/image-header.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
  }
}
.header__logo {
  width: 100%;
}
@media (max-width: 48em) {
  .header__logo {
    display: none;
  }
}
.header__logo-mobile {
  width: 100%;
  display: none;
}
@media (max-width: 48em) {
  .header__logo-mobile {
    display: block;
  }
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2.125rem 3rem 0 2.5rem;
}
@media (max-width: 48em) {
  .header__container {
    padding: 2rem 1.5rem 0 1.5rem;
  }
}
.header__nav-container {
  display: flex;
  gap: 2.9375rem;
  align-items: center;
}
@media (max-width: 64em) {
  .header__nav-container {
    display: none;
  }
}
.header__nav {
  display: flex;
  gap: 2.9375rem;
  align-items: center;
}
.header__nav--link {
  color: var(--white);
}
.header__mobile-menu {
  display: none;
  position: relative;
}
@media (max-width: 64em) {
  .header__mobile-menu {
    display: block;
  }
}
.header__mobile-menu span {
  display: block;
  width: 1.5rem;
  height: 0.125rem;
  background-color: var(--white);
  margin-bottom: 0.375rem;
}
.header__mobile-menu span:last-child {
  margin-bottom: 0;
}
.header__mobile-nav--container {
  position: absolute;
  top: 3.25rem;
  right: 0;
  width: 20.5625rem;
  background-color: #fffdfa;
  text-align: center;
}
.header__mobile-nav--container::before {
  position: absolute;
  top: -1.5rem;
  right: 0;
  content: "";
  width: 0;
  height: 0;
  border-bottom: 1.5rem solid #fffdfa;
  border-left: 1.5rem solid transparent;
}
.header__nav-mobile {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  margin-top: 2.4375rem;
}
.header__nav-mobile--link {
  color: var(--darkGrayishBlue);
}
.header .mobile-btn {
  background-color: var(--yellow);
  border: 1px solid var(--yellow);
  margin-top: 2rem;
  margin-bottom: 2.4375rem;
}
.header__contact-button {
  border: 1px solid var(--white);
  border-radius: 1.75rem;
  background-color: var(--white);
  padding: 1rem 1.875rem 0.9375rem 2rem;
  font-family: var(--font-fraunces);
  font-weight: 700;
  font-size: 0.9375rem;
  line-height: 1.5625rem;
  letter-spacing: -0.11px;
  color: var(--veryDarkDesaturatedBlue);
  text-transform: uppercase;
  cursor: pointer;
  transition: all 250ms ease-in-out;
}
.header__contact-button:hover {
  border: 1px solid rgba(255, 255, 255, 0.1);
  background-color: rgba(255, 255, 255, 0.2);
  color: var(--white);
}
.header__title {
  text-align: center;
  padding-top: 6.5625rem;
}
@media (max-width: 64em) {
  .header__title {
    padding-top: 5.5rem;
  }
}
.header__arrow {
  width: 1.875rem;
  padding-top: 6.03375rem;
  margin: 0 auto;
}
@media (max-width: 64em) {
  .header__arrow {
    padding-top: 3.25rem;
  }
}
.header .hidden {
  display: none;
}
.header .hamburger-menu {
  cursor: pointer;
}

.feature {
  width: 100%;
  background-color: #f2f2f2;
  display: grid;
  grid-template: minmax(430px, 600px) minmax(430px, 600px) 37.5rem/repeat(2, 1fr);
  justify-items: center;
}
@media (max-width: 48em) {
  .feature {
    grid-template-columns: 1fr;
    grid-template-rows: 23.4375rem 1fr 23.4375rem 1fr 37.5rem 37.5rem;
  }
}
.feature__item-brand, .feature__item-audience {
  width: 27.8125rem;
  margin-block: auto;
  color: var(--darkGrayishBlue);
}
.feature__item-brand p, .feature__item-audience p {
  margin-bottom: 2.3em;
}
@media (max-width: 64em) {
  .feature__item-brand, .feature__item-audience {
    text-align: center;
    width: 20.4375rem;
  }
}
@media (max-width: 48em) {
  .feature__item-brand, .feature__item-audience {
    text-align: center;
    margin-block: 3.75rem;
  }
}
.feature__link {
  font-family: var(--font-fraunces);
  font-weight: 900;
  font-size: 15px;
  line-height: 1.5625rem;
  letter-spacing: 1px;
  color: var(--veryDarkDesaturatedBlue);
  text-transform: uppercase;
  position: relative;
  z-index: 1;
  cursor: pointer;
}
.feature__link::after {
  content: "";
  width: 8.5625rem;
  height: 0.625rem;
  border-radius: 1.75rem;
  position: absolute;
  top: 0.6875rem;
  right: -0.5rem;
  z-index: -1;
  opacity: 0.25;
  transition: opacity 350ms ease-in-out;
}
.feature__link-yellow::after {
  background-color: var(--yellow);
}
.feature__link-yellow:hover::after {
  opacity: 1;
}
.feature__link-pink::after {
  background-color: var(--softRed);
}
.feature__link-pink:hover::after {
  opacity: 1;
}
.feature__image--yellow {
  width: 100%;
  height: 100%;
  background-image: url("../images/desktop/image-transform.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
@media (max-width: 48em) {
  .feature__image--yellow {
    grid-row: 1;
  }
}
.feature__image--pink {
  width: 100%;
  height: 100%;
  background-image: url("../images/desktop/image-stand-out.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.feature__item {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.feature__item-secondary {
  max-width: 21.1875rem;
  text-align: center;
  margin-bottom: 3em;
}
.feature__item-design {
  background-image: url("../images/desktop/image-graphic-design.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  color: var(--darkDesaturatedCyan);
}
@media (max-width: 64em) {
  .feature__item-design {
    background-image: url("../images/mobile/image-graphic-design.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
  }
}
.feature__item-photography {
  background-image: url("../images/desktop/image-photography.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  color: var(--darkBlue);
}
@media (max-width: 64em) {
  .feature__item-photography {
    background-image: url("../images/mobile/image-photography.jpg");
    background-repeat: no-repeat;
    background-size: cover;
  }
}

.testimonials {
  background-color: #f2f2f2;
}
.testimonials__items {
  width: 80%;
  max-width: 69.375rem;
  margin: 0 auto;
  display: flex;
  gap: 1.25rem;
  align-items: center;
  padding-top: 5.0625rem;
  padding-bottom: 10rem;
}
@media (max-width: 64em) {
  .testimonials__items {
    width: 20.4375rem;
    flex-direction: column;
    gap: 4rem;
    padding-top: 4rem;
    padding-bottom: 5.375rem;
  }
}
.testimonials__item {
  text-align: center;
}
.testimonials__item--photo {
  display: block;
  margin: 0 auto;
  border-radius: 100%;
  width: 4.5rem;
  height: 4.5rem;
}
.testimonials__item--feedback {
  line-height: 2rem;
  color: var(--veryDarkGrayishBlue);
  padding-top: 4.25rem;
}
@media (max-width: 64em) {
  .testimonials__item--feedback {
    padding-top: 2rem;
  }
}
.testimonials__item--name {
  font-family: var(--font-fraunces);
  line-height: 1.386875rem;
  padding-top: 4.3125rem;
}
@media (max-width: 64em) {
  .testimonials__item--name {
    padding-top: 2rem;
  }
}
.testimonials__item--occupation {
  font-size: 0.875rem;
  color: var(--grayishBlue);
  line-height: 1.5625rem;
  padding-top: 0.5625rem;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}
@media (max-width: 48em) {
  .gallery {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

.footer {
  background-color: #91d4c7;
}
.footer__items {
  width: 18.5625rem;
  margin: 0 auto;
  padding: 4.5rem 0;
}
.footer__logo {
  margin: 0 auto;
}
.footer__info-links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 2.5rem;
}
.footer__info-link {
  color: var(--darkModerateCyan);
}
.footer__info-link:hover {
  color: var(--white);
}
.footer__socials {
  display: flex;
  gap: 1.75rem;
  justify-content: center;
  align-items: center;
  padding-top: 5.5rem;
}
.footer__socials .social-icon {
  transition: filter 300ms ease-in-out;
}
.footer__socials .social-icon:hover {
  filter: brightness(0) invert(1);
}