@use '../util' as *;

.feature {
    width: 100%;
    background-color: #f2f2f2;
    display: grid;
    grid-template: minmax(430px, 600px) minmax(430px, 600px) rem(600)/ repeat(2, 1fr);
    justify-items: center;

    @include breakpoint-down(small) {
        grid-template-columns: 1fr;
        grid-template-rows: rem(375) 1fr rem(375) 1fr rem(600) rem(600);
    }

    &__item-brand,
    &__item-audience {
        width: rem(445);
        margin-block: auto;
        color: var(--darkGrayishBlue);

        p {
            margin-bottom: 2.3em;
        }

        @include breakpoint-down(medium) {
            text-align: center;
            width: rem(327);
        }

        @include breakpoint-down(small) {
            text-align: center;
            margin-block: rem(60);
        }
    }

    &__link {
        font-family: var(--font-fraunces);
        font-weight: 900;
        font-size: 15px;
        line-height: rem(25);
        letter-spacing: 1px;
        color: var(--veryDarkDesaturatedBlue);
        text-transform: uppercase;
        position: relative;
        z-index: 1;
        cursor: pointer;
        

        &::after {
            content: '';
            width: rem(137);
            height: rem(10);
            border-radius: rem(28);
            position: absolute;
            top: rem(11);
            right: rem(-8);
            z-index: -1;
            opacity: 0.25;
            transition: opacity 350ms ease-in-out;
        }
        
    }

    &__link-yellow::after {
        background-color: var(--yellow);
    } 

    &__link-yellow:hover::after {
        opacity: 1;
    } 

    &__link-pink::after {
        background-color: var(--softRed);
    } 

    &__link-pink:hover::after {
        opacity: 1;
    } 

    &__image--yellow {
        width: 100%;
        height: 100%;
        background-image: url('../images/desktop/image-transform.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;

        @include breakpoint-down(small) {
            grid-row: 1;
        }
    }

    &__image--pink {
        width: 100%;
        height: 100%;
        background-image: url('../images/desktop/image-stand-out.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
    }

    &__item {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: flex-end;
     }
 

    &__item-secondary {
        max-width: rem(339);
        text-align: center;
        margin-bottom: 3em;
    }

    &__item-design {
        background-image: url('../images/desktop/image-graphic-design.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
        color: var(--darkDesaturatedCyan);

        @include breakpoint-down(medium) {
            background-image: url('../images/mobile/image-graphic-design.jpg');
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center center;
        }
    }

    &__item-photography {
        background-image: url('../images/desktop/image-photography.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
        color: var(--darkBlue);

        @include breakpoint-down(medium) {
            background-image: url('../images/mobile/image-photography.jpg');
            background-repeat: no-repeat;
            background-size: cover;
        }
    }
    
}