@use '../util' as *;

.testimonials {
    background-color: hsl(0, 0%, 95%);
    

    &__items {
        width: 80%;
        max-width: rem(1110);
        margin: 0 auto;
        display: flex;
        gap: rem(20);
        align-items: center;
        padding-top: rem(81);
        padding-bottom: rem(160);

        @include breakpoint-down(medium) {
            width: rem(327);
            flex-direction: column;
            gap: rem(64);
            padding-top: rem(64);
            padding-bottom: rem(86);
        }
    }

    &__item {
        text-align: center;
    }  

    &__item--photo {
        display: block;
        margin: 0 auto;
        border-radius: 100%;
        width: rem(72);
        height: rem(72);
    }

    &__item--feedback {
        line-height: rem(32);
        color: var(--veryDarkGrayishBlue);
        padding-top: rem(68);

        @include breakpoint-down(medium) {
            padding-top: rem(32);
        }
    }

    &__item--name {
        font-family: var(--font-fraunces);
        line-height: rem(22.19);
        padding-top: rem(69);

        @include breakpoint-down(medium) {
            padding-top: rem(32);
        }
    }

    &__item--occupation {
        font-size: rem(14);
        color: var(--grayishBlue);
        line-height: rem(25);
        padding-top: rem(9);
    }
    
}